.treeIcons {
    position: absolute;
    top: 7%;
    left: 67%;
    display: flex;
    display: none;
    flex-direction: column;
    background-color: darkslategrey;
    border-radius: 16px;
    border: 15px;
    border-style: groove;
    border-color: burlywood;
    padding: 1%;
    box-shadow: -1px 1px 20px 11px #a9ff003b;
}
 
.parentIcons {
    width: 74%;
    margin: 0 auto;
    text-align: center;
    padding: 2% 0%;
    display: flex;
    justify-content: space-evenly;
}

.childrenIcons {
    text-align: center;
    padding: 2% 0%;
    display: flex;
    justify-content: space-evenly;
}

.grandChildrenIcons {
    display: flex;
    justify-content: space-evenly;
}