.treeKey {
    height: auto;
    /* temporary height below*/
}

#keyHeader {
    text-align: center;
    margin: -4px -7px 0;
    border-radius: 5px 5px 0 0;
    background-color: gold;
    padding: 3% 0;
    /* display to none for now may bring back */
    display: none;
}

/* .ui.grid{
    flex-wrap: nowrap;
} */

#keyGenerationColors{
    /* justify-content: space-evenly; */
}

div#itemLabelMine {
    align-self: center;
}

#keyGenerationColors #colorSelectorMine {
    margin:0; 
    padding:0; 
}

#keyGenerationColors #colorSelectorMine .item{
    width: auto;
    padding: 5px;
    border-radius: 50%;
    margin: 3px 3px;
}

#minimizerIcons {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

@media screen and (min-width: 768px) {
    .treeKey {
        /* border: 3px solid #0072ff70; */
        border-radius: 5px;
        padding: 0% 0.3%;
    }
    #keyGenerationColors .item {
        padding: 0;
        padding-right: 7px;
    }
    div#itemLabelMine {
        text-align: center;
    }
}
@media screen and (min-width: 1000px) {
    .treeKey {
        height:162px; 
        /* width: 17%;  */
        width: 26%; 
        min-width: 175px;
        position: absolute;
        top:1%;
        right:7%;
        /* border: 3px solid #0072ff70; */
        border-radius: 5px;
        padding: 0% 0.3% 9%;
    }
}